img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif, "Arial";
  font-size: 14px;
  line-height: 1.2; }

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/inter/inter-400.woff2") format("woff2"), url("../fonts/inter/inter-400.woff") format("woff"); }

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/inter/inter-700.woff2") format("woff2"), url("../fonts/inter/inter-700.woff") format("woff"); }

.page {
  height: 100%;
  background-color: #ffffff; }

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  max-width: 1220px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 10px; }

.header {
  margin-bottom: 100px; }

.header__inner {
  display: flex;
  justify-content: space-between; }

.menu__list {
  list-style: none;
  margin: 0 60px 0 0;
  padding: 0;
  display: flex; }

.menu__item:not(:last-child) {
  margin-right: 10px; }

.menu__link {
  display: block;
  text-decoration: none;
  color: inherit;
  padding: 41px 20px;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 17px;
  transition: all 0.3s ease-in-out; }
  .menu__link:hover {
    color: #ffffff;
    background-color: #131313; }

.logo_image {
  vertical-align: top; }

.bike {
  margin-bottom: 150px; }

.bike__inner {
  display: flex;
  justify-content: space-between; }

.bike__column_slider {
  flex-basis: 630px;
  max-width: 630px; }

.bike__column_description {
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }

.title {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 64px;
  line-height: 77px; }

.bike__subtitle {
  margin-bottom: 60px;
  font-size: 32px;
  line-height: 39px;
  color: #6f6f6f; }

.bike__description {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 80px;
  max-width: 420px; }

.bike__text {
  text-align: justify; }

.bike__list {
  display: flex;
  justify-content: space-around;
  max-width: 500px;
  margin: 0 auto;
  list-style: none; }

.bike__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-width: 110px;
  min-height: 53px; }
  .bike__item dt {
    order: 2;
    margin: auto 0;
    font-size: 12px; }
  .bike__item dd {
    margin-bottom: 5px;
    font-weight: 700;
    text-transform: uppercase; }

.bike-slider {
  margin-bottom: 25px; }

.bike-slider .slick-dots {
  top: 52px;
  right: 0; }

.slick-dots {
  position: absolute;
  top: 0;
  list-style: none; }
  .slick-dots li:not(:last-child) {
    margin-bottom: 10px; }
  .slick-dots button {
    font-size: 0;
    width: 4px;
    height: 25px;
    border: none;
    border-radius: 7px;
    background-color: #131313;
    cursor: pointer;
    transition: all 0.3s ease-in-out; }
    .slick-dots button:hover {
      background-color: #ff4040; }

.slick-active button {
  background-color: #ff4040; }

.footer {
  min-height: 200px;
  background-color: #e5e5e5; }

.button {
  display: inline-block;
  width: 420px;
  border: none;
  padding: 28px 70px;
  color: #ffffff;
  background-color: #131313;
  font-size: 20px;
  letter-spacing: 1.1em;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  cursor: pointer; }
  .button:hover {
    background-color: #ff4040; }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }
